<template>
	<div>
		<Table
				border
				ref="selection"
				:columns="columns"
				:data="shoppingCart"
				size="large"
				no-data-text="您的购物车没有商品，请先添加商品到购物车"
				@on-selection-change="selectChange"
		>
			<template slot-scope="{ row,index }" slot="count">
				<InputNumber
						:min="1"
						:max="shoppingCart[index].Quota !== -1 ? shoppingCart[index].Quota : 10000"
						v-model="shoppingCart[index].GoodsCount"
						:precision="0"
						@on-change="handleChange($event,index)"
				></InputNumber>
			</template>
			<template slot-scope="{ row, index }" slot="readMore">
				<Button type="error" size="small" @click="remove(index)">删除</Button>
			</template>
		</Table>

		<div class="go-to">
			<span style="font-size: 16px; margin-right: 30px">商品总量：{{totalCount}}</span>
			<span style="font-size: 16px;">商品总价：￥{{totalPrice}}</span>
		</div>

		<div class="go-to">
			<Button @click="deleteSelect" :disabled="!selectData.length" type="error" style="margin-right: 30px">删除选中</Button>
			<Button @click="goTo" :disabled="!shoppingCart.length" type="primary">去下单</Button>
		</div>
	</div>
</template>

<script>
	import {mapActions, mapState} from "vuex";

	export default {
		name: "MyShoppingCart",

		data() {
			return {
				columns: [
					{
						type: "selection",
						width: 50,
						align: "center"
					},
					{
						title: "图片",
						key: "GoodsImg",
						width: 80,
						render: (h, params) => {
							return h("div", [
								h("img", {
									attrs: {
										src: params.row.GoodsImg,
										width: 40,
										height: 40,
									}
								})
							]);
						},
						align: "center"
					},
					{
						type: 'html',
						title: "条码",
						width: 250,
						key: "SerialNumber",
						align: "center"
					},
					{
						title: "商品",
						key: "GoodsName",
						align: "center"
					},
					{
						title: "属性",
						width: 300,
						key: "specs",
						align: "center"
					},
					{
						title: "数量",
						width: 150,
						slot: "count",
						align: "center"
					},
					{
						title: "金额",
						width: 150,
						key: "Price",
						align: "center"
					},
					{
						title: "小计",
						width: 150,
						key: "totalPrice",
						align: "center"
					},
					{
						title: "操作",
						width: 150,
						slot: "readMore",
						align: "center"
					}
				],
				selectData: []
			};
		},

		computed: {
			...mapState(["shoppingCart"]),

			totalCount: {
				get() {
					let total = 0;
					this.shoppingCart.forEach(item => {
						total += item.GoodsCount;
					});
					return total;
				}
			},

			totalPrice: {
				get() {
					let total = 0;
					this.shoppingCart.forEach(item => {
						total += item.GoodsCount * item.Price;
					});
					return total;
				}
			},
		},

		async created() {
			await this.loadShoppingCart();
		},

		methods: {
			...mapActions(["loadShoppingCart"]),

			deleteSelect() {
				this.$Modal.confirm({
					title: "购物车",
					content: "确认删除选中的商品？",
					onOk: async () => {
						let data = [];
						this.selectData.forEach(item => {
							data.push(item.CartID);
						});
						let res = await this.$request.deleteCart(data);
						if (res) {
							this.$Message.info("删除成功");
							await this.loadShoppingCart();
						}
					},
					onCancel: () => {}
				});
			},

			goTo() {
				this.$router.push("/order");
			},

			remove(index) {
				this.$Modal.confirm({
					title: "购物车",
					content: "确认删除此商品？",
					onOk: async () => {
						let res = await this.$request.deleteCart([this.shoppingCart[index].CartID]);
						if (res) {
							this.$Message.info("删除成功");
							await this.loadShoppingCart();
						}
					},
					onCancel: () => {}
				});
			},

			handleChange(e, i, id) {
				this.$nextTick(async () => {
					let value = e;
					if (!value) {
						value = 1;
					}
					this.shoppingCart[i].GoodsCount = value;
					this.shoppingCart[i].totalPrice = value * this.shoppingCart[i].Price;

					let specs = [];
					this.shoppingCart[i].GoodsValueList.forEach(item => {
						let tmp = {
							'CategoryType': item.CategoryType,
							'GoodsValueID': item.GoodsValueID,
						};
						specs.push(tmp);
					});

					await this.$request.updateCartCount(this.shoppingCart[i].GoodsID, this.shoppingCart[i].GoodsCount, specs);
				});
			},

			selectChange(event) {
				this.selectData = event;
			},
		},
	};
</script>

<style scoped>
	.go-to {
		margin: 15px;
		display: flex;
		justify-content: flex-end;
	}
</style>



